import {createStore} from 'vuex'
import {logout} from "@/api/ucenter/login";
import {message} from 'ant-design-vue'
import router from "@/router";

const store = createStore({
  state: {
    LoginState: !!localStorage.getItem('loginState'),
    UserInfo: localStorage.getItem('userInfo') ? localStorage.getItem('userInfo') : {},
  },
  mutations: {
    //同步操作
    // 更新用户信息和状态·
    updateUser(state, userinfo) {
      state.LoginState = true;
      state.UserInfo = userinfo;
      localStorage.setItem('userInfo', JSON.stringify(userinfo))
      // localStorage.setItem('userInfo', userinfo)

      localStorage.setItem('apiAuth', userinfo.token)
      localStorage.setItem('loginState', true)
    },
    // 账号退出
    logout(state) {
      logout().then(res => {
        message.success(res.msg);
        state.LoginState = false;
        localStorage.setItem('loginState', false)
        localStorage.removeItem('userInfo')
        localStorage.removeItem('apiAuth')
        router.push('/')
      })

    }
  },
  actions: {
    //异步操作
  },
  //getters相当于计算属性
  getters: {
    // 获取用户登录状态
    getUserState(state) {
      return state.LoginState
    }
  }
})
export default store
