<template>
  <div class="main">
    <a-spin tip="Loading..." :spinning="Loading">
      <a-row :gutter="12" style="padding:10px">
        <a-col class="gutter-row" :xs="24" :md="18" :span="18">
          <a-card>
            <a-typography-title style="font-size: 25px">{{ data.title }}</a-typography-title>
            <a-divider/>
            <div>
              <a-space>
                <span> {{ data.create_time_text }}更新</span>
                <a-tag>
                  <EyeOutlined/>
                  88阅读
                </a-tag>
                <a-tag>
                  <LikeOutlined/>
                  99点赞
                </a-tag>
              </a-space>
            </div>
            <a-list item-layout="horizontal">
              <a-space direction="vertical">
                <span> 网盘分类：{{ data.pan }}</span>
                <span>资源分类：{{ data.category }}</span>
                <span>  资源标签：{{ data.tag }}</span>
                <span>  资源链接：<a @click="Copy" class="link"
                                    :data-clipboard-text="currentRoutePath">{{ currentRoutePath }}</a> </span>
              </a-space>
            </a-list>
          </a-card>
          <a-card>
            <p class="item-tips" v-html="data.content"></p>
            <!--            {{ $route.params.id }}-->
          </a-card>
        </a-col>
        <a-col class="gutter-row" :xs="24" :md="6" :span="6">
          <div class="gutter-box">
            <div>
              <Download :id="$route.params.id"/>
              <SearchSide/>

              <a-card title="11登录" :bordered="false">
                <p>Card content</p>
                <p>Card content</p>
                <p>Card content</p>
              </a-card>
            </div>
          </div>
        </a-col>
      </a-row>
    </a-spin>
  </div>
</template>
<script setup>
import {ref, onMounted} from 'vue'
import {EyeOutlined, LikeOutlined} from '@ant-design/icons-vue'
import SearchSide from "@/components/SearchSide.vue";
import Download from "@/components/Download.vue";
import Clipboard from 'clipboard';
import {message} from 'ant-design-vue';
import {useRoute} from 'vue-router';
import router from "@/router";
import {getItem} from "@/api/resource";
// 全局loading动画
const Loading = ref(true);
const route = useRoute();
const data = ref({});
const currentRoutePath = ref(window.location.href);

onMounted(() => {
  let id = route.params.id;
  getItem(id).then(res => {
    if (!res.data) {
      router.push('/404')
    }
    data.value = res.data;

    // setTimeout(function () {
    Loading.value = false;
    // }, 900)
  }).catch(err => {
    console.log(err)
  })
  // document.onselectstart = new Function("event.returnValue=false");
});


// 复制字符串
const Copy = (e) => {
  const clipboard = new Clipboard('.link');
  clipboard.on('success', () => {
    message.success('复制成功');
    clipboard.destroy(); // 销毁 clipboard 实例，释放资源
  });

  clipboard.on('error', (e) => {
    message.error('复制失败');
    clipboard.destroy(); // 销毁 clipboard 实例，释放资源
  });
}
</script>
<style scoped>

</style>
<script setup>
</script>
