<template>
  <div class="login-box">
    <div class="login-logo">
      <!-- <svg-icon name="logo" :size="45" /> -->
      <img src="https://buqiyuan.gitee.io/vue3-antdv-admin/assets/logo-3503d5cd.png" width="45"/>
      <h1 class="mb-0 ml-2 text-3xl font-bold">Antd Admin</h1>
    </div>
    <a-tabs v-model:activeKey="activeKey">
      <a-tab-pane key="1" tab="账号密码登录" style="justify-content:center">
        <a-form ref="formRef"
                :model="loginForm"
                :rules="rules">
          <a-form-item ref="account" name="account">
            <a-input size="large"  v-model:value="loginForm.account">
            </a-input>
          </a-form-item>
          <a-form-item ref="password" name="password">
            <a-input
                type="password"
                v-model:value="loginForm.password"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
                placeholder="验证码"
                :maxlength="4"
                v-model:value="loginForm.captcha"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-button type="primary" @click="handleSubmit" size="large" :loading="loginBtn" block>
              登录
            </a-button>
          </a-form-item>
        </a-form>

      </a-tab-pane>
      <a-tab-pane key="2" tab="邮箱 登录" force-render>
        <a-form ref="emailFormRef"
                :model="emailForm"
                :rules="emailRules">
          <a-form-item ref="account" name="account">
            <a-input size="large"  v-model:value="emailForm.email">
            </a-input>
          </a-form-item>
          <a-form-item ref="password" name="password">
            <a-input
                type="password"
                v-model:value="emailForm.password"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
                placeholder="验证码"
                :maxlength="4"
                v-model:value="loginForm.captcha"
            >
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-button type="primary" @click="handleSubmit" size="large" :loading="loginBtn" block>
              登录
            </a-button>
          </a-form-item>
        </a-form>

      </a-tab-pane>
      <!--      <a-tab-pane key="3" tab="Tab 3">Content of Tab Pane 3</a-tab-pane>-->
    </a-tabs>
  </div>
</template>

<script setup>
// import {login} from '@/api/user/login'
import {message} from 'ant-design-vue';
import router from "@/router";
import {reactive, ref, toRaw} from 'vue';
import {handleLogin} from "@/api/ucenter/login";
import store from "@/store";

const formRef = ref();
const emailFormRef = ref();
// 登录表单
const loginForm = reactive({});
const emailForm = reactive({});
const rules = {
  account: [
    {
      required: true,
      message: 'Please input name',
      trigger: 'change',
    },
  ],
  password: [
    {
      required: true,
      message: 'Please select region',
      trigger: 'change',
    },
  ]
};
const emailRules = {
  email: [
    {
      required: true,
      message: 'Please input name',
      trigger: 'change',
    },
  ],
  password: [
    {
      required: true,
      message: 'Please select region',
      trigger: 'change',
    },
  ]
};
// 登录按钮状态
const loginBtn = ref(false);
const handleSubmit = () => {
  loginBtn.value = true;
  formRef.value
      .validate()
      .then(() => {
        handleLogin(loginForm).then(res => {
          if (res.code == 1) {
            store.commit('updateUser', res.data.userinfo)
            router.push('/');
          }
          setTimeout(function () {
            loginBtn.value = false
          }, 200)
          message.success(res.msg);
        });
      })
      .catch(error => {
        message.error('请将信息填写完成');
      });
  loginBtn.value = false
}

</script>

<style lang="less" scoped>
.login-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding-top: 240px;
  background: url('https://buqiyuan.gitee.io/vue3-antdv-admin/assets/login-dbbe1096.svg');
  background-size: 100%;

  .login-logo {
    display: flex;
    align-items: center;
    margin-bottom: 30px;

    .svg-icon {
      font-size: 48px;
    }
  }

  :deep(.ant-form) {
    width: 400px;

    .ant-col {
      width: 100%;
    }

    .ant-form-item-label {
      padding-right: 6px;
    }
  }
}
</style>
