<template>
  <a-card title="搜索" :bordered="false" style="padding: 0 8px;margin-bottom: 12px">

    <a-input-search
        v-model:value="keyword"
        placeholder="宁肯少字不要错字"
        enter-button
        @search="handleSearch"
    />
  </a-card>
</template>

<script setup>
import {ref} from 'vue'
import router from "@/router";
import {message} from 'ant-design-vue'

const keyword = ref('');
const handleSearch = () => {
  if (keyword.value) {
    router.push('/search=' + keyword.value)
  } else {
    message.error('请输入搜索词');
  }


}

</script>
