// 创建新的axios实例
import axios from "axios";
import {message} from 'ant-design-vue'

const service = axios.create({
  // 环境变量，需要在.env文件中配置
  // baseURL: process.env.VUE_APP_BASE_API,
  baseURL: '/api',
  // 超时时间暂定5s
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
    // 'token': store.state.UserInfo.token ? store.state.UserInfo.token : ''
    'token': localStorage.getItem('apiAuth') ? localStorage.getItem('apiAuth') : '',
  },
});
// service.interceptors.request.use(
//     config => {
//       // 此处添加Loading
//       return config;
//     },
//     error => {
//       return Promise.reject(error);
//     }
// );

const Request = (url, options = {}) => {
  let method = options.method || 'get';
  let params = options.params || {};

  if (method === 'get' || method === 'GET') {
    return new Promise((resolve, reject) => {
      service
          .get(url, {
            params: params,
          })
          .then(res => {
            if (res && res.data) {
              resolve(res.data);
            }
          })
          .catch(err => {
            reject(err);
          });
    });
  } else {
    return new Promise((resolve, reject) => {
      service
          .post(url, params)
          .then(res => {
            if (res && res.data) {
              // console.log(res)
              resolve(res.data);
            }
          })
          .catch(err => {
            // console.log(err)
            message.error(err.response.data.msg)
            reject(err);
          });
    });
  }
};

export default Request;
