<template>
  <Header/>
  <div class="main">
    <a-card>
      <a-row :gutter="12" style="padding:10px">
        <a-col class="gutter-row" :xs="24" :md="18" :span="18">
          <a-input-search
              style="margin-bottom: 10px"
              v-model:value="value"
              placeholder="请输入关键字"
              enter-button="搜索"
              size="large"
              @search="onSearch"
          />
          <ResourceList ref="searchSide" :keyword="keyword"/>
        </a-col>
        <a-col class="gutter-row" :xs="24" :md="6" :span="6">
          333
        </a-col>
      </a-row>
    </a-card>
  </div>
</template>
<script setup>
import Header from "@/pages/layouts/components/Header.vue";
import {useRoute} from 'vue-router';
import {ref} from 'vue'
import ResourceList from "@/components/ResourceList.vue";

const route = useRoute();
const keyword = route.params.id;
const value = ref(keyword ? keyword : '');
const searchSide = ref(null)
const onSearch = () => {
  // console.log(searchSide.value.search(value.value))
  searchSide.value.search(value.value)
}
</script>
