import http from '@/api/request'

export function getHotRes(params) {
  return http('/index/getHotRes', {
    method: 'POST',
    params: params,
  });
}

export function getItem(id) {
  return http('/resource/getItem', {
    method: 'POST',
    params: {
      id
    },
  });
}

/**
 * 获取下载链接
 * @param id
 * @returns {Promise<unknown>}
 */
export function getLink(id) {
  return http('/resource/download', {
    method: 'POST',
    params: {
      id
    },
  });
}
