<template>
  <a-card title="资源下载" :bordered="false" style="padding: 0 8px;margin-bottom: 12px">
    <a-row :gutter="16">
      <a-col class="gutter-row" :span="14">
        <a-button type="primary" style="width: 100%" :loading="DownBtnLoading"
                  @click="handleDownlaod" :disabled="!UserState">
          <template #icon>
            <CloudDownloadOutlined/>
          </template>
          {{ UserState ? '立即下载' : '您未登录' }}
        </a-button>
        <!--        <div class="gutter-box">col-6</div>-->
      </a-col>
      <a-col class="gutter-row" :span="10">
        <a-button style="width: 100%" :loading="InspectBtnLoading" @click="hangleUrl">
          <template #icon>
            <QuestionCircleOutlined/>
          </template>
          检测
        </a-button>
      </a-col>
    </a-row>
    <a-row>
<!--      <a-col :span="24">-->
<!--        22-->
<!--      </a-col>-->
    </a-row>
  </a-card>
</template>
<script setup>
import {ref, defineProps} from 'vue'
import {message} from 'ant-design-vue'
import {CloudDownloadOutlined, QuestionCircleOutlined} from '@ant-design/icons-vue'
import {useStore} from 'vuex';
import {getLink} from "@/api/resource";
import router from "@/router";

const props = defineProps({
  id: Number,
})
const store = useStore();
const UserState = store.state.LoginState;
// 下载按钮Loading
const DownBtnLoading = ref(false);
// 检测链接是否失效Loading
const InspectBtnLoading = ref(false);

// 获取下载链接
const handleDownlaod = () => {
  DownBtnLoading.value = true;
  getLink(props.id).then(res => {
    if (res.code !== 1) {
      DownBtnLoading.value = false;
      message.error(res.msg)
      // DownBtnLoading.value = true;
    }
    message.success(res.msg)
    DownBtnLoading.value = false;
    setTimeout(function () {
      window.open(res.data.down_link, '_blank');
    }, 800)

  })


}

// 检测链接是否失效
const hangleUrl = () => {
  InspectBtnLoading.value = true;
  setTimeout(function () {
    message.success('当前链接正常');
    InspectBtnLoading.value = false;
  }, 800)
}

</script>
