import {
  createRouter,
  createWebHashHistory
} from 'vue-router'

import Layout from "@/pages/layouts/Layout.vue";
// 仪表盘首页
import Index from '@/pages/system/Index.vue'
// 管理员信息
// import MyInfo from '@/view/system/myinfo.vue'
// import Login from '@/view/system/login.vue'
import resourceItem from '@/pages/resource/item.vue'
import NotFound from '@/pages/404.vue'
import resourceAll from '@/pages/resource/All.vue'
import Userinfo from "@/pages/ucenter/userinfo.vue";
import Search from "@/pages/system/Search.vue";
import Demo1 from "@/pages/demo1.vue";
import Login from "@/pages/ucenter/login.vue";
import Register from "@/pages/ucenter/register.vue";
import subjectAll from "@/pages/subject/All.vue";

const whiteList = ['/login', '/system/login'];

const routes = [
  {
    path: "/",
    component: Layout,
    // 子路由
    children: [{
      path: "/",
      component: Index,
      meta: {
        title: "后台首页"
      }
    }, {
      path: "/resource",
      component: resourceAll,
      props: true,
      meta: {
        title: "资源详情页",
        keepAlive: true,
      }
    },{
      path: "/subject",
      component: subjectAll,
      props: true,
      meta: {
        title: "专题页",
        keepAlive: true,
      }
    }, {
      path: "/resource/:id",
      component: resourceItem,
      props: true,
      meta: {
        title: "资源详情页",
        keepAlive: true,
      },
    }, {
      path: "/ucenter/userinfo",
      component: Userinfo,
      props: true,
      meta: {
        title: "用户中心",
        requireAuth: true,
        keepAlive: true,
      }
    }, {
      path: "/sea11rch=:id",
      component: Userinfo,
      props: true,
      meta: {
        title: "用户中心",
        keepAlive: true,
      }
    }]
  }, {
    path: "/demo1",
    component: Demo1,
    props: true,
    meta: {
      title: "搜索",
      keepAlive: true,
    }
  }, {
    path: "/ucenter/login",
    component: Login,
    props: true,
    meta: {
      title: "搜索",
      keepAlive: true,
    }
  }, {
    path: "/ucenter/register",
    component: Register,
    props: true,
    meta: {
      title: "搜索",
      keepAlive: true,
    }
  }, {
    path: "/search=:id",
    component: Search,
    props: true,
    meta: {
      title: "搜索",
      keepAlive: true,
    }
  }, {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: NotFound
  }]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
router.beforeEach((to, from, next) => {
  console.log(to)
  window.document.title = to.meta.title == undefined ? '默认标题' : to.meta.title
  if (to.meta.requireAuth) {
    let token = localStorage.getItem('apiAuth');
    let userInfo = localStorage.getItem('userInfo');
    if (!token || !userInfo) {
      next({
        path: '/ucenter/login',
      })
    }
  }
  next();
})
