import http from '@/api/request'

/**
 * 账号密码登录
 * @param params
 * @returns {Promise<unknown>}
 */
export function handleLogin(params) {
  return http('/user/login', {
    method: 'POST',
    params: params,
  });
}

/**
 * 注册接口
 * @param params
 * @returns {Promise<unknown>}
 */
export function handleRegister(params) {
  return http('/user/register', {
    method: 'POST',
    params: params,
  });
}

export function logout(params) {
  return http('/user/logout', {
    method: 'POST',
    params: params,
  });
}

