<template>
  <div>
    <a-card>
      <!--      000——{{ props.id }}-->
      <a-list item-layout="horizontal" :data-source="data">
        <template #renderItem="{ item }">
          <a-list-item>
            <a-list-item-meta>
              <template #title>
                <a @click="getContent(item.id)">{{ item.title }}</a>
              </template>
              <template #avatar>
                <a-tag color="#18BC9C">{{ item.pan }}</a-tag>
              </template>
            </a-list-item-meta>
          </a-list-item>
        </template>
      </a-list>
      <a-pagination v-model:current="pageData.page" v-model:pageSize="pageData.limit" :total="total" show-less-items
                    @change="changePage"/>
    </a-card>
  </div>

</template>
<script setup>
import {ref, onMounted, defineExpose} from 'vue'
import router from "@/router";
import {getList} from "@/api/resource";
// 接受父组件id参数
const props = defineProps({
  id: Number,
  keyword: String
})
const data = ref([]);
const total = ref();
// 分页数据
const pageData = ref({
  page: '1',
  limit: '30',
  keyword: props.keyword ? props.keyword : '',
  pan: '',
  tag: ''
})
onMounted(() => {
  console.log(props.keyword)
  if (props.id) {
    getData();
  } else if (props.keyword) {
    console.log('搜索')
    getData();
  }
});
const changePage = () => {
  getData();
}
// 搜索事件
const search = (key) => {
  console.log(key)
  pageData.value.keyword = key;
  getData()
}
const getData = (p, t) => {
  // console.log(p)
  pageData.value.pan = p;
  pageData.value.tag = t;
  getList(pageData.value).then(res => {
    data.value = res.data.list
    total.value = res.data.count
  }).catch(err => {
    console.log(err)
  });
}
// 跳转资源详情页
const getContent = (resId) => {
  // console.log(resId)
  router.push('/resource/' + resId)
}
defineExpose({getData, search})
</script>
<style scoped>

</style>
