<template>
  <div>
    <a-row :gutter="16" type="flex" justify="end">
      <!--      <a-col :span="3" v-if="editBtn">-->
      <!--        <a-button @click="closeEdit">取消</a-button>-->
      <!--      </a-col>-->
      <a-col :span="3">
        <a-button v-if="editBtn" @click="closeEdit">取消</a-button>

      </a-col>
      <a-col :span="3">
        <a-button v-if="!editBtn" @click="handleEdit">更新</a-button>
        <a-button v-if="editBtn" type="primary" @click="onsubmit">保存</a-button>
      </a-col>
    </a-row>
  </div>
  <a-form
      :model="formState"
      name="basic"
      :label-col="{ span: 8 }"
      :wrapper-col="{ span: 16 }"
      autocomplete="off"
      @finish="onFinish"
      @finishFailed="onFinishFailed"
  >
    <a-row :gutter="16" style="margin-top: 20px">
      <a-col class="gutter-row" :span="10">
        <a-form-item
            label="用户名"
            name="username"
        >
          <a-input v-model:value="formState.username"/>
        </a-form-item>


        <a-form-item
            label="邮箱"
            name="email"
        >
          <a-input-group compact>
            <a-input disabled v-model:value="formState.email" style="width: calc(120% - 80px)"/>
            <a-tooltip title="去验证">
              <a-button @click="handleVerify">
                <template #icon>
                  <QuestionCircleOutlined/>
                </template>
              </a-button>
            </a-tooltip>
          </a-input-group>
          <!--          <a-input v-model:value="formState.email"/>-->
        </a-form-item>
      </a-col>
      <a-col class="gutter-row" :span="10">
        <a-form-item
            label="昵称"
            name="nickname"
        >
          <a-input v-model:value="formState.uid"/>
        </a-form-item>
        <a-form-item
            label="密码"
            name="password"
        >
          <a-input-password placeholder="留空则不更新密码" v-model:value="formState.password"/>
        </a-form-item>
      </a-col>

    </a-row>
    <!--    <a-form-item :wrapper-col="{ offset: 8, span: 16 }">-->
    <!--      <a-button >Submit</a-button>-->
    <!--    </a-form-item>-->
  </a-form>
  <a-modal v-model:open="verifyEmailModal" title="验证邮箱" @ok="handleOk">
    <a-form
        :model="verifyEmailForm"
        name="basic"
        autocomplete="off"
        @finish="onFinish"
        @finishFailed="onFinishFailed"
    >
      <a-form-item
          label="我的邮箱"
          name="email"
          :rules="[{ required: true, type:'email',message: '请输入正确的邮箱格式' }]"
      >
        <a-input v-model:value="verifyEmailForm.email"/>
      </a-form-item>

      <a-form-item
          label="验证码"
          name="password"
      >
        <a-input v-model:value="verifyEmailForm.cha"/>
      </a-form-item>
    </a-form>
  </a-modal>
</template>
<script setup>
import {ref, reactive} from 'vue';
import {QuestionCircleOutlined} from '@ant-design/icons-vue'

const verifyEmailModal = ref(false);
const editBtn = ref(false);
const verifyEmailForm = ref({
  email:'',
  cha:''
})
// 打开验证邮箱Modal
const handleVerify = () => {
  verifyEmailModal.value = true

}
const handleEdit = () => {
  editBtn.value = true;
}
const closeEdit = () => {
  editBtn.value = false;
}
// const formState = reactive({
//   username: '',
//   password: '',
//   remember: true,
// });
const formState = JSON.parse(localStorage.getItem('userInfo'));
const onFinish = values => {
  console.log('Success:', values);
};
const onFinishFailed = errorInfo => {
  console.log('Failed:', errorInfo);
};
</script>
