<template>
  <div>
    <a-card title="分类">
      <div style="margin-bottom: 25px">
        <a-typography-text strong style="margin-right: 40px">
          网盘分类
        </a-typography-text>
        <a-tag class="custom-tag" v-for="(tag,index) in panTags" :key="index" :color="selectedPanTag==tag.pan?'blue':''"
               @click="checkTag(0, tag.pan)">
          {{ tag.pan }}
        </a-tag>
      </div>
      <div>
        <a-typography-text strong style="margin-right: 40px">
          资源分类
        </a-typography-text>
        <a-tag v-for="(tag,index) in resourceTags" :key="index" class="custom-tag"
               :color="selectedresourceTag==index?'blue':''"
               @click="checkTag(1,index)">
          {{ tag.name }}
        </a-tag>
      </div>
    </a-card>
  </div>
</template>
<script setup>
import {ref, onMounted, defineEmits} from 'vue'
import {getCategory, getPan} from "@/api/category/category";

const selectedPanTag = ref('全部');
const selectedresourceTag = ref('0');
const panTags = ref([]);
const resourceTags = ref([]);
const emit = defineEmits(['closeson']);
onMounted(() => {
  getPan().then(res => {
    panTags.value = res.data
  })
  getCategory().then(res => {
        resourceTags.value = res.data
      }
  )
})
const checkTag = (type, e) => {
  console.log(e)
  switch (type) {
    case 0:
      selectedPanTag.value = e
      handleCheck()
      break;
    case 1:
      selectedresourceTag.value = e
      handleCheck()
      break;
  }
}
// const
const handleCheck = () => {
  emit('closeson', selectedPanTag.value, selectedresourceTag.value)
  console.log(selectedPanTag.value)
}
</script>
<style scoped>
.custom-tag {
  font-size: 12px; /* 调整字体大小，可以根据需要自定义 */
  padding: 3px 10px; /* 调整内边距，可以根据需要自定义 */
}
</style>
